.App {
  text-align: center;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(79deg, #7439db, #C66FBC 48%, #F7944D);
}

.auth-form-container, .login-form, .register-form {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 600px) {
  .auth-form-container {
    padding: 5rem;
    border: 1px solid white;
    border-radius: 10px;
    margin: 0.5rem;
  }
}

.login-register-switch {
  padding: 0.25rem 0.5rem;
  font-size: 0.75rem;
  width: 100px;
}

/* Sidebar styling */
.sidebar {
  position: fixed;
  top: 0;
  left: 10px;

  
}

/* Menu styling */
.menu ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.menu ul li {
  margin: 15px 0;
}

.menu ul li a {
  color: #fff;
  text-decoration: none;
}

.menu ul li a:hover {
  color: #ddd;
}

/* Dashboard content styling */
.dashboard-content {
  margin-left: 250px; /* Adjust content to start after the sidebar */
  padding: 20px;
}


.card-title {
  font-size: 1.2rem;
  color: #333;
}

.card-text {
  color: #666;
  font-size: 0.95rem;
}

.btn-danger {
  background-color: #b12626;
}

.btn-danger:hover {
  background-color: #9e2222;
}
  